<template>
  <div
    :class="myClass"
    class="page-400"
  >
    <div class="center-box">
      <div class="left">
        <img
          :src="imgUrl"
          alt="404图片"
        >
      </div>
      <div class="right">
        <h1 class="note-error">
          {{ msg.title||'404' }}
        </h1>
        <h2 class="note-result">
          {{ msg.content||'页面不存在' }}
        </h2>
        <h4 class="note-link">
          {{ msg.notice||'如有问题请联系管理员' }}
        </h4>
        <gs-button
          v-if="buttonConfig"
          type="primary"
          class="btn"
          @click="buttonConfig.callback"
        >
          {{ buttonConfig.name||'返回首页' }}
        </gs-button>
      </div>
    </div>
  </div>
</template>

<script>
import './404-page.scss';

export default {
  props: {
    'imgUrl': {
      type: String,
      required: true,
      default: ''
    },
    'msg': {
      type: Object,
      default: () => {
        return {
          title: null,
          content: null,
          notice: null
        };
      }
    },
    'myClass': {
      type: String,
      default: ''
    },
    'buttonConfig': {
      type: Object,
      default() {
        return {};
      }
    }
  },
  data() {
    return {};
  }
};
</script>
